<div class="grid grid-cols-1 gap-2 sm:grid-cols-2 sm:gap-0 md:gap-2 md:grid-cols-3 lg:grid-cols-4"><template v-for="(i, index) in youTubeData" :key="i.title + index"><a class="flex flex-col" href="#"><div class="relative"><img class="object-cover w-full aspect-video" :src="i.mainPicture" :alt="i.title + &quot;picture&quot;"><span class="absolute bottom-1 right-1 text-white rounded bg-slate-800 text-xs px-1">{{i.videoTime}}</span></div><div class="flex flex-row gap-3 mt-3"><img class="img rounded-full object-cover w-36px h-36px" v-if="i.subPicture !== ''" :src="i.subPicture" :alt="i.title + &quot;picture&quot;"><div class="grow from-group flex flex-col"><h3 class="text-base lineClamp">{{i.title}}</h3><span class="text-xs text-slate-500">{{i.subTitle}}</span><div class="flex flex-row"></div><span class="text-xs text-slate-500">觀看次數: {{i.views}}次．{{i.time}}前</span></div><span><i class="bi bi-heart" @click="addFavorite(i)"></i></span></div></a></template></div>